
















































































import { Component, Vue } from "vue-property-decorator";
import { IDoor } from "../../types/Store";
import axios from "../../services/axios";
import { mapGetters } from "vuex";
import { errorFunc } from "../../utils/errorFunc";
import {VForm} from '../../types/Main'

name: "DoorAddOrEdit"

@Component
export default class DoorAddOrEdit extends Vue {
  private door = {
    name: "",
    price: null,
    status: false,
    id: "",
  } as IDoor;
  private valid = false;
  private nameRules = [(v) => !!v || "Это поле не может быть пустым"];
  private loading = true;
  private load = false;

  public close(): void {
    this.$router.push("/reference/door");
  }

  public editDoor(): void {
    this.$store.dispatch("editedDoor", {
      name: this.door.name,
      price: this.door.price,
      status: this.door.status,
      id: this.door.id,
    });
    this.close();
    this.$forceUpdate()
  }

  public async addNewDoor(): Promise<any> {
    this.form.validate();
    if (this.valid) {
      try {
        await this.$store
          .dispatch("createDoor", {
            name: this.door.name,
            price: this.door.price,
            status: this.door.status,
          })
          .then(() => {
            this.$router.push("/reference/door");
          });
          this.$forceUpdate()
      } catch (error) {
        let msg = errorFunc(this.$store.state.error);
        this.loading = false;
        let toast = this.$toasted.show(msg, {
          theme: "bubble",
          position: "top-center",
          duration: 5000,
        });
      }
    } else {
      return;
    }
  }

  get isEdit(): boolean {
    if (this.$route.params.id) {
      return true;
    } else {
      return false;
    }
  }

  get viewMethod() {
    if (this.isEdit) {
      return this.editDoor;
    } else {
      return this.addNewDoor;
    }
  }

  get form(): VForm {
    return this.$refs.form as VForm;
  }

  async created() {
    if (this.isEdit) {
      this.loading = true;
      try {
        let { data } = await axios.get(`/api/v1/door/${this.$route.params.id}`);
        this.door = data;
        this.loading = false;
      } catch (error) {
        this.loading = false;
        this.$router.push({ name: "notFound" });
      }
    } else {
      this.loading = false;
    }
  }
}
